<template>
	<div id="opinion">
		<mt-header fixed title="">
			<router-link to="/userinfo" slot="left">
         	 	<mt-button icon="back"></mt-button>
     	 	</router-link>
		</mt-header>
		<div class="breakbox"></div>
		<div class="reportBox" v-if="submitStatus">
			<div class="typeBox">
				<div class="typeName">
					
				</div>
				<div class="typeConfirm">
					<div @click="checkType(1)" v-bind:class="{active:typeCheck==1}"></div>
					<div @click="checkType(2)" v-bind:class="{active:typeCheck==2}"></div>
					<div @click="checkType(3) " v-bind:class="{active:typeCheck==3}"></div>
					<div @click="checkType(4) " v-bind:class="{active:typeCheck==4}"></div>
				</div>
			</div>
			<div class="textareaBox">
				<textarea placeholder="......" v-model="question" maxlength="150">
				</textarea>
				<div class="cell">
	                <div class="cell_hd"><label class="label"></label></div>
	                <div class="cell_bd">
	                    <input v-model="mobile" oninput="if(value.length>18)value=value.slice(0,18)" type="number" placeholder="">
	                </div>
	            </div>
			</div>
			<div class="clearFloat">
				<div class="textareaNum">
					<span  v-text="question.length">0</span>/150
				</div>
			</div>
			<div id="container" class="reportImgs">
				<div class="introImg fl pickfilesBox" style="margin-right: 0!important">
					<div id="pickfiles" style="margin-right: 0">
						<img src="../../../assets/users/icon/addImg.png"/>
					</div>
					
				</div>
			</div>
			<mt-button @click="formClick" type="danger" class="btnClass"></mt-button>
		</div>
 		<div v-else class="prodStatusBox" >
       		<div class="prodStatus">
       			<div class="statusImg">
       				<img src="../../../assets/users/icon/icon_successSymbol.png" />
       			</div>
   				<div class="statusTest">
   					<span>!!</span>
   				</div>
       		</div>
       		<p></p>
       		<p></p>
       	</div>
		<div class="breakbox"></div>
		<div class="gallery" @click="galleryRes()">
        	<span>
        		<img src="" alt="">
        	</span>
        </div>
	</div>
</template>
<script>
	import { Indicator } from 'mint-ui';
	import { Toast } from 'mint-ui';
	 import { MessageBox } from 'mint-ui';
	export default{
		name:'opinion',
		data(){
			return{
				image:'',
				mobile:'',
				uploader:'',
				basePhone:'',
				question:'',
				submitStatus:'',
				typeCheck:1,
				dataList:'',
				qinniuToken:'',
				thisHtml:'',
				imgLink:'',
				api:{
					getQiniuToken:'/genuine/user/getUploadToken',
					creatReport:'/genuine/feedback/addFeedback'
					
				},
				icons:{

				},
				directory:''

			}
		},
		mounted(){
			$(window).scrollTop(0);
		},
		created(){
			this.submitStatus=true;
			this.getQiniuToken();
			var _this = this;
			$(document).on('click','i',function(){
				$(this).parent().remove();
                var toremove = '';
                var id = $(this).attr("data-val");
                var uploader = _this.uploader;
                for (var i in uploader.files) {
                    if (uploader.files[i].id === id) {
                        toremove = i;
                    }
                }
                if($('.introImg').length < 4){
                	$('.moxie-shim').show()
                	$('.pickfilesBox').show();
                }
                if($('.introImg').length == 3){
                	$('.moxie-shim').css('margin-left','0');
                }
                if($('.introImg').length == 2){
                	$('.moxie-shim').css('margin-left','-33%');
                }
                if($('.introImg').length == 1){
                	$('.moxie-shim').css('margin-left','-66%');
                }
                 uploader.files.splice(toremove, 1);
			}).on("click",'.introIcon',function(){
				_this.gallery1($(this));
			});
		},
		methods:{
			getQiniuToken:function(){
				var params = {};
				params.bucket = "zpsy";
				var _this =this;
		      	this.$http({
      				  method:'POST',
			url:this.api.getQiniuToken,
			  		  body:JSON.stringify(params),
			  		  headers:{source:'H5'},
			  		  dataType:'json',
					  before:function(){
						Indicator.open('...');
					  }
		}).then(function(res){
					Indicator.close();
					var data=res.data;
					if(data.errorCode=="0"){
							_this.qinniuToken = data.result;
							_this.directory=data.result.directory;

							this.init();
					}else{
						Toast(data.reason)
					}
				},function(res){
					Indicator.close();
					Toast({
			            message: ',',
			            position: 'middle',
			            duration: 1000
					});
				})
			},
			//pluploadmOxie
			//mOxiehttps://github.com/moxiecode/moxie/wiki/API
			//
			previewImage:function (file,callback){
			//filepluploadfile,callback
				if(!file || !/image\//.test(file.type)) return; //
				if(file.type=='image/gif'){//gifFileReader,mOxie.Imagejpgpng
					var fr = new mOxie.FileReader();
					fr.onload = function(){
						callback(fr.result);
						fr.destroy();
						fr = null;
					}
					fr.readAsDataURL(file.getSource());
				}else{
					var preloader = new mOxie.Image();
					preloader.onload = function() {
						preloader.downsize( 300, 300 );//,300300
						var imgsrc = preloader.type=='image/jpeg' ? preloader.getAsDataURL('image/jpeg',80) : preloader.getAsDataURL(); //src,base64
						callback && callback(imgsrc); //callbackurl
						preloader.destroy();
						preloader = null;
					};
					preloader.load( file.getSource() );
				}	
			},
			//
			init:function(){
				var _this = this;
				_this.html = $('#container').html();
				_this.uploader = Qiniu.uploader({
				    runtimes: 'html5,flash,html4',      // 
				    browse_button: 'pickfiles',         // 
				    // uptokenuptoken_urluptoken_func
				    // uptoken > uptoken_url > uptoken_func
				    // uptokenuptoken_urluptokenuptoken_func
				    // uptoken : '<Your upload token>', // uptoken
				    // uptoken_url: '/uptoken',         // AjaxuptokenUrl
				    // uptoken_func: function(file){    // uptoken
				    //    // do something
				    //    return uptoken;
				    // },
				    uptoken:this.qinniuToken.token,
				    get_new_uptoken: false,             // uptoken
				    // downtoken_url: '/downtoken',
				    // AjaxdownTokenUrlJS-SDKPOSTkeydomainJSONurlurl
				    unique_names: false,              // falsekeyJS-SDKkey
				    save_key: false,                  // falseuptokensava_keySDKkey
				    domain: this.qinniuToken.domain
,     // bucket
				    container: 'container',             // DOM IDbrowser_button
				    max_file_size: '100mb',             // 
				    flash_swf_url: '../../../../dist/lib/plupload/js/Moxie.swf',  //flash
				    max_retries: 3,                     // 
				    dragdrop: false,                     // 
				    drop_element: 'container',          // ID
				    chunk_size: '4mb',                  // 
				    auto_start: false,                   // 
				    //x_vars : {
				    //    
				    //    'time' : function(up,file) {
				    //        var time = (new Date()).getTime();
				              // do something with 'time'
				    //        return time;
				    //    },
				    //    'size' : function(up,file) {
				    //        var size = file.size;
				              // do something with 'size'
				    //        return size;
				    //    }
				    //},
				    init: {
				        'FilesAdded': function(up, files) {
			                var msgFlg = 0;
			                for (var i = 0, len = files.length; i < len; i++) {
		                        !function (i) {
		                            _this.previewImage(files[i], function (imgsrc) {
		                                $('#container').prepend('<div class="introImg fl"><i data-value='+ files[i].id +'>x</i><img class="introIcon" src="'+ imgsrc +'" /></div>');
		                            })
		                            $('.moxie-shim').css('margin-left','34%');
		                            if($('.introImg').length >=3){
		                            	$('.moxie-shim').hide()
		                            	$('.pickfilesBox').hide();
		                            }
		                        }(i);
			                }
				        },
				        'BeforeUpload': function(up, file) {
				        	Indicator.open('');
				               // 
				        },
				        'UploadProgress': function(up, file) {
				               // 
				        },
				        'FileUploaded': function(up, file, info) {
				        	// var progress = new FileProgress(file, 'fsUploadProgress');
                			// progress.setComplete(up, info);
				               // 
				               // infojson
				               // {
				               //    "hash": "Fh8xVqod2MQ1mocfI4S4KpRL6D98",
				               //    "key": "gogopher.jpg"
				               //  }
				               // 
				               var domain = up.getOption('domain');
				               var res = JSON.parse(info);
				               //Url
				               _this.image = _this.image + domain+'/'+res.key+',';
				               // _this.image  = _this.image.substr(0,_this.image .length - 1); 
				        },
				        'Error': function(up, err, errTip) {
				               //
				               Toast({
						            message: ',',
						            position: 'middle',
						            duration: 1000
								});
				        },
				        'UploadComplete': function() {
				        	Indicator.close();
				               //
				               _this.image  = _this.image.substr(0,_this.image.length - 1); 
				               _this.formSubmit();
				        },
				        'Key': function(up, file) {
				            // key
				            // unique_names: falsesave_key: false
				            var key = Date.parse(new Date())+Math.ceil(Math.random()*1000);
				            // do something with key here
				            var nKey=_this.directory+key
				            return nKey
				        }
				    }
				});
			},
			gallery1(event){
				var src = event.attr('src');
				var st = $(window).scrollTop();
				$('.gallery span img').attr('src', src);
				$('.gallery').css({
					'visibility': 'visible',
					'top': st
				});
			},
	      	gallery(event){
				var src = event.toElement.src;
				var st = $(window).scrollTop();
				$('.gallery span img').attr('src', src);
				$('.gallery').css({
					'visibility': 'visible',
					'top': st
				});
//				$('body').css({
//					' overflow-y':'hidden'
//				})
			},
			 //
		    galleryRes:function(){
		    	$('.gallery').css({
					'visibility': 'hidden'
				});
//				$('body').css({
//					' overflow-y':'auto'
//				})
		    },
		    checkType:function(typeNumber){
		    	this.typeCheck = typeNumber;
		    },
		    formClick:function(){
	    		var _this = this;
		    	_this.uploader.start();
		    },
		    formSubmit:function(){
		    	if(!this.question || !this.mobile){
		    		Toast({
			            message: '!',
			            position: 'middle',
			            duration: 1000
					});
		    		return false;
		    	}
		    	var params = {};
				// 0 :1: 2: 3:
				params.type = this.typeCheck;
				params.question = this.question;
				params.image = this.image;
				params.mobile = this.mobile;
				var _this =this;
		      	this.$http({
      				  method:'POST',
			url:this.api.creatReport,
			  		  body:JSON.stringify(params),
			  		  dataType:'json',
					  before:function(){
						Indicator.open('...');
					  }
		}).then(function(res){
					Indicator.close();
					var data=res.data;
					if(data.errorCode=="0"){
						this.submitStatus = !this.submitStatus;
					}else{
						Toast(data.reason)
					}
				},function(res){
					Indicator.close();
					Toast({
			            message: ',',
			            position: 'middle',
			            duration: 1000
					});
				})
		    }
		}
	}
</script>
<style lang="sass">
	#opinion{
		.typeBox{
			margin: 10px 4%;
			font-size:0.9em;
			.typeConfirm{
				position: relative;
				overflow: hidden;
				margin: 10px 0;
				
				div{
				    float: left;
				    width: 50%;
				    text-align: center;
				    padding: 10px;
				    background: #fff;
				    color: #333;
				    &:nth-of-type(1){
						border-right: 1px solid #dbdbdb;
						border-bottom: 1px solid #dbdbdb;
 					}
 					 &:nth-of-type(2){
						border-bottom: 1px solid #dbdbdb;
 					}
 					&:nth-of-type(3){
						border-right: 1px solid #dbdbdb;
 					}
				}
				div.active{
					background: #aaa;
					color: #fff;
				}
			}
		}
		.prodStatusBox{
			height: auto;
			word-wrap: break-word;
			padding: 30px;
			text-align: center;
			margin-top: 30%;
			img{
				width:30px;
				height: 30px;
			}
			.prodStatus{
				overflow: hidden;
			    margin: 20px;
				.statusImg{
				    float: left;
				    width: 40%;
				    text-align: right;
				}
				.statusTest{
				    padding: 10px;
				    margin-left: 42%;
				    text-align: left;
				}
			}
			p{
			    
    			line-height: initial;
			}
		}
		.btnClass{
		    width: 80%;
		    margin: 20px 10%;
		    background: #c12;
		    height: 50px;
		    font-size: .9em;
		}
		.clearFloat{
			overflow:hidden;
		}
		.reportImgs{
		    width: 97%;
		    height: 100px;
		    margin: 0 10px;
			.introImg{
				position:relative;
				width:33.33% ;
				display: block;
				padding-bottom: 10px;
				padding-top: 10px;
			    padding-right: 10px;
				min-height: 120px;
				box-sizing: border-box;
				max-height: 8rem;
				i{
					position: absolute;
				    right: 10px;
				    top: 10px;
				    width: 12px;
				    height: 12px;
				    font-size: 12px;
				    z-index: 3;
				    border-radius: 100%;
				    background-color: #fb1245;
				    color: #fff;
				    text-align: center;
				    line-height: 12px;
				}
				img{
					height: 100px;
					width: 100%;
				}
			}
		}
		.textareaNum{
			float: right;
			margin-right: 4%;
		}
		.textareaBox{
			width:92%;
			margin:5% 4%;
			border: 1px solid #e3e3e3;
		    line-height: 30px;
		    background: #fff;
			textarea{
				font-size: .9em;
				width: 100%;
				height: 200px;
				resize: none;
			    border: 0px;
    			border-bottom: 1px solid #e3e3e3;
			}
			.phoneBox{
				width: 100%;
				input:{
					width: 100%;
				}
			}
			.cell{
			    align-items: center;
			    line-height: normal;
			    position: relative;
			    display: flex;
			    padding: 0 15px;
			    line-height: 50px;
			    background: #fff;
			    .cell_hd{
			    	width: 25%;
				    font-size: 14px;
				    color: #666;
				    label{
			    	    padding-right: 8px;
    					border-right: 1px solid #e3e3e3;
				    }
			    }
			    .cell_bd{
				    flex: 1;
				    input{
			    	    width: 100%;
					    line-height: 1.8;
					    height: 50px;
					    font-size: 14px;
					    outline: 0;
					    border: 0;
				    }
			    }
			}
		}
		.mint-cell-wrapper{
			display: list-item;
		}
		.mint-cell-value{
			display:block;
		}
		.userDefault{
		  		padding: 5px;
		  		background: #fff url(../../../assets/users/icon/icon_nav_goto_black.png) no-repeat right 10px top 10px;
		  		background-size: 50px auto;
		  		overflow: hidden;
		  		height:auto;
		  		img{
			    	float: left;
				    width: 50px;
				    height: 50px;
				    margin: 10px;
		  		}
		  		.userBox{
		  			margin: 10px 30px 10px 80px;
		  			div{
		  				margin-top: 10px;
		  			}
		  		}
		  	}
	}
</style>


